*{
   margin: 0;
   padding: 0;
}
body{
   background-color: #a840ff;
}
ul{
   list-style-type: none;
}
.wrapper{
   display: flex;   
   justify-content: center;
   flex-direction: column;
   align-items: center;
   max-width: 1280px;
   margin: 0 auto;
   -webkit-box-flex: 0;
}
.title{
   text-align: center;
}
.todo-btn__delete{
   padding: 2px 20px;
   background-color: rgba(255, 0, 0, 0.603);
   border: 0px;
   color: white;
}
ul li{
   margin: 10px;
}
.todo__input{
   padding: 12px 20px;
   margin-bottom: 30px;
   border-radius: 20px;
   font-size: 100%;
}
.new-todo{
   position: relative;
   width: 400px;
   height: 150px;
   max-height: 250px;
   background-color: #eee;
   overflow: auto;
   -webkit-transition: all .25s ;
   transition: all .25s ;
   border-radius: 2%;
   -webkit-box-shadow: 0 30px 30px 20px;
   box-shadow: 0 12px 30px ;
   h3{
      margin: 20px;
   }
}
.todo-list{
   display: flex;
   justify-content: center;
   flex-wrap: wrap;
}
.todo-btn{
   display: flex;
   justify-content: space-between;
   margin: 20px;
   position: absolute;
   bottom: 0;
   button{
      margin: 0 10px 0 0;
   }
}
.filter-btn{
   display: flex;
}
.todo-form{
   input{
      margin: 0 20px;
   }
}

